body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 75%;
  margin: 32px auto;
  padding: 32px;
  border-style: solid;
  border-width: 1px;
  border-radius: 16px;
}

.note {
  padding-top: 8px;
  font-size: 16px;
}

.error {
  color: red;
  background: #fff;
  font-size: 20px;
  width: 50%;
  padding: 10px;
  margin: auto;
  text-align: center;
  border-style: solid;
  border-width: 2px;
  border-radius: 16px;  
}
